body {
    font-family: "Playfair Display", serif;
}
.header-with-menu {
    .header {
        width: 100%;
        height: 50px;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fa9f98;
    }
    .user-management {
        position: absolute;
        top: 9px;
        right: 30px;
        display: flex;
        gap: 10px;
        .btn {
            padding: 5px;
            border-radius: 10px;
            background-color: rgba(0, 0, 0, 0);
            border-color: #ffe2e0;
            color: #ffe2e0;
        }
        .btn:hover {
            opacity: 0.7;
            cursor: pointer;
        }
    }
    .user-logout {
        position: absolute;
        top: 9px;
        right: 30px;
        display: flex;
        .btn {
            padding: 5px;
            border-radius: 10px;
            background-color: rgba(0, 0, 0, 0);
            border-color: #ffe2e0;
            color: #ffe2e0;
        }
        .btn:hover {
            opacity: 0.7;
            cursor: pointer;
        }
    }
}
.header-with-menu:hover {
    opacity: 0.8;
    cursor: pointer;
}
.menu {
    margin-top: 10px;
    margin-inline: 100px;
    border-bottom: 1px solid #666666;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    .calender-link,
    .question-link {
        text-decoration: none;
        list-style: none;
        font-weight: bolder;
        margin-bottom: 5px;
        text-align: center;
        color: #000
    }
    .calender-link:hover,
    .question-link:hover {
        cursor: pointer;
        opacity: 0.5;
    }
}
.health-care-menu {
    margin-top: 10px;
    margin-inline: 5%;
    border-bottom: 1px solid #666666;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    .calender-link {
        text-decoration: none;
        font-weight: bolder;
        margin-bottom: 5px;
        text-align: center;
        color: #000
    }
    .calender-link:hover {
        cursor: pointer;
        opacity: 0.5;
    }
}
/* NewRegistrationコンポーネント */
.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    margin-top: 20px;
    width: 300px;
    height: 250px;
    border: 2px solid #fa9f98;
    border-radius: 20px;
    .error-message {
        position: relative;
        font-size: 10px;
        color: red;
    }
    .user-id {
        display: flex;
        flex-direction: column;
        margin-top: 10px;
        input {
            width: 250px;
        }
    }
    .password {
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        input {
            width: 250px;
        }
    }
    .forgot-password {
        font-size: 12px;
        width: 100%;
        text-align: end;
    }
    .submit-btn {
        width: 100px;
        height: 30px;
        display: block;
        margin: 40px auto 0;
        background-color: #fa9f98;
        border: 1px solid #000;
        border-radius: 5px;
    }
    .submit-btn:hover {
        cursor: pointer;
        opacity: 0.8;
    }
    .submit-btn:disabled {
        cursor: not-allowed;
        opacity: 0.8;
    }
    .eye-img {
        width: 20px;
        position: relative;
        top: -90px;
        left: 110px;
    }
    .eye-img:hover {
        cursor: pointer;
    }
}
/* calender, MemoPaperコンポーネント */
.health-care {
    margin-inline: 25px;
    .select-year-month {
        .select-year {
            border-style: none none solid;
            font-size: 16px;
            margin-right: 15px;
        }
        .select-year:hover {
            opacity: 0.7;
            cursor: pointer;
        }
        .select-month {
            border-style: none none solid;
            font-size: 16px;
        }
        .select-month:hover {
            opacity: 0.7;
            cursor: pointer;
        }
    }
    .calender {
        margin-top: 30px;
        padding: 5px;
        display: grid;
        grid-template-columns: repeat(7, 1fr);
        gap: 2px;
        border: 1px solid #000;
        .day {
            height: 100px;
            border: 1px solid #000;
        }
        .day:hover {
            opacity: 0.5;
            cursor: pointer;
        }
        .saturday {
            background-color: rgba(5, 38, 252, 0.1);
        }
        .sunday {
            background-color: rgba(252, 5, 5, 0.2);
        }
        .day-text {
            position: relative;
            top: -25px;
            left: 5px;
            font-size: 20px;
        }
        .hospital-schedule {
            font-size: 10px;
            margin-inline: 5px;
        }
    }
}
.back-calender {
    display: inline-block;
    text-decoration: none;
    margin-top: 20px;
    color: #fa9f98;
    border-bottom: 1px solid #fa9f98;
    margin-left: 20px;
}
.back-calender:hover {
    opacity: 0.7;
    cursor: pointer;
}
.current-date {
    margin-left: 20px;
    margin-top: 10px;
    margin-bottom: 0;
    font-family: serif;
}
.memo-paper {
    display: flex;
    gap: 20px;
    margin-top: 5px;
    margin-inline: 20px;
    padding: 10px;
    border: 1px solid #000;
    border-radius: 20px;
    p {
        margin-bottom: 5px;
    }
    textarea {
        resize: none;
        field-sizing: content;
        min-width: 290px;
        min-height: 50px;
        max-height: 200px;
        overflow: auto;
    }
    .weight {
        textarea {
            min-width: 50px;
            min-height: 20px;
            font-size: 20px;
            text-align: center;
        }
    }
}
.save-btn {
    text-align: center;
    button {
        margin-top: 20px;
        width: 100px;
        height: 40px;
        padding: 5px;
        border-radius: 10px;
        background-color: #fa9f98;
        border-color: #ffe2e0;
        color: #fff;
    }
    button:hover {
        opacity: 0.7;
        cursor: pointer;
    }
}
/* WeightGraphコンポーネント */
.weight-graph {
    .select-year-month {
        .select-year {
            border-style: none none solid;
            font-size: 16px;
            margin-right: 15px;
        }
        .select-year:hover {
            opacity: 0.7;
            cursor: pointer;
        }
        .select-month {
            border-style: none none solid;
            font-size: 16px;
        }
        .select-month:hover {
            opacity: 0.7;
            cursor: pointer;
        }
    }
}
/* QuestionToAiコンポーネント */
.question-to-ai {
    .cautionary-note {
        display: flex;
        justify-content: center;
        margin-inline: 30px;
        p {
            color: red;
        }
    }
    .question-form {
        display: flex;
        justify-content: center;
        .question-container {
            width: 80%;
            height: 25px;
            padding: 10px;
            resize: none;
            border-radius: 15px 0 0 15px;
            scrollbar-width: none;
        }
        .question-box::-webkit-scrollbar {
            display: none;
        }
        .send-button {
            background: none;
            background-color: #404040;
            border: none;
            border-radius: 0 15px 15px 0;
            .material-symbols-outlined {
                /* font-variation-settings: 
                'FILL' 0,
                'wght' 400,
                'GRAD' 0,
                'opsz' 24 */
                color: #fa9f98;
            }
        }
        .send-button:hover {
            opacity: 0.8;
            cursor: pointer;
        }
        .send-button:active {
            transform: scale(0.98);
        }
    }
    .tag {
        margin-top: 30px;
        margin-left: 3%;
        margin-bottom: 0;
    }
    .loading-animation span{
        display: inline-block;
        animation-name: wave;
        animation-duration: 3s;
        animation-iteration-count: infinite;
        animation-timing-function: ease-in-out;
    }
    hr {
        width: 94%;
    }
    .ai-response {
        margin-inline: 25px;
    }
}

@media screen and (max-width: 500px) {
    .memo-paper {
        display: block;
    }
}

@keyframes wave {
    0% {
        transform: translateY(0);
    }
    25% {
        transform: translateY(-5px);
    }
    50%, 100% {
        transform: translateY(0);
    }
}
.loading-animation span:nth-child(1) {animation-delay: 0.1s;}
.loading-animation span:nth-child(2) {animation-delay: 0.3s;}
.loading-animation span:nth-child(3) {animation-delay: 0.5s;}
.loading-animation span:nth-child(4) {animation-delay: 0.7s;}
.loading-animation span:nth-child(5) {animation-delay: 0.9s;}
.loading-animation span:nth-child(6) {animation-delay: 1.1s;}
.loading-animation span:nth-child(7) {animation-delay: 1.3s;}
.loading-animation span:nth-child(8) {animation-delay: 1.5s;}